<template>
  <div class="guaranty-detail">
    <div class="guaranty-info ">
      <p class=" number-state">
        <span class="number">订单编号：{{detail.order_sn}}</span>
        <span class="state">{{groupStatus(detail.status)}}</span>
      </p>
      <div class="title-logo ">
        <p class="company-title">{{detail.plan_name}}</p>
        <p class="logo"><img :src="baseSrc+detail.companyLogo" alt="公司logo"></p>
      </div>

      <div class="info-wrapper info-order-wrapper">
        <p class="info" v-if="detail.policy_num">保单号：{{detail.policy_num}}</p>
        <p class="info">投保人：{{detail.companyName}}</p>
        <p class="info">被保险人：{{userInfo.name}}</p>
        <p class="info">保障期间：{{detail.policy_effective_date}}至{{detail.policy_expiration_date}}</p>

      </div>
     <!-- <p class="price">{{detail.premium}}<span>元</span></p>-->
    </div>
    <div class="guaranty-content ">
      <div class="layer bzfa-layer" >
        <div class="title-name " @click="bzfaShow=!bzfaShow"    >
          <span class="title">保障方案</span>
          <p class="name" v-if="detail.plan_name">
            <span>{{detail_guarantee.name}}</span>
            <i :style="bzfaShow ? upImg : downImg"></i>
          </p>
        </div>
        <div class="bzfa-wrapper" v-show="bzfaShow&&bzfaInfo.length" v-for="(item,index) in bzfaInfo" :key="index">
          <div class="plan-info-item">
            <div class="oneDiv" @click="detailShow(index)">
              <span class="bzxm">{{item.item}} </span>
              <div class="bzsm">
                <span>{{item.quota}}</span>
                <i :style="moreArr[index] ? upImg : downImg"></i>
              </div>
            </div>
            <div class="twoDiv" v-show="moreArr[index]">
              <div class="bzed">
                <span>{{item.description}}</span>
              </div>
            </div>

          </div>
        </div>

      </div>
      <div class="layer layer-bttr">
        <div class="title-name" @click="bttrShow=!bttrShow">
          <span class="title">被保险人信息</span>
          <p class="name">
            <span class="name">{{detail_member.name}}</span>
            <i :style="bttrShow ? upImg : downImg"></i>
          </p>
        </div>
        <div class="info-wrapper" v-show="bttrShow">
          <p class="info "><span>姓名</span><span>{{detail_member.name}}</span></p>
          <p class="info "><span>性别</span><span>{{getSexByName(detail_member.gender)}}</span></p>
          <p class="info "><span>身份证号</span><span>{{detail_member.id_num}}</span></p>
          <p class="info "><span>出生日期</span><span>{{dateFormat(detail_member.birth)}}</span></p>
          <p class="info "><span>联系电话</span><span>{{detail_member.mobile}}</span></p>
        </div>
      </div>
      <div class="layer layer-claim" v-show='detail.claim_flow'>
        <p class="title-ts">
          理赔流程
        </p>
        <div class="claim-content">
          <div v-html='detail.claim_flow'></div>
        </div>
      </div>
      <div class="layer "  v-show='detail.prompt_info'>
        <p class="title-ts">
          提示信息
        </p>
        <div class="tip-content" v-html='detail.prompt_info'>
        </div>
      </div>
      <div class="btns" v-if="detail.status==3">
        <div class="btn toClaim" @click="goClaim(detail)">申请理赔</div>
        <!--<div class="btn baodan" v-if="orderDetail.policy_url!==null">电子保单</div>-->
        <!--<div class="btn againPay" v-if="[5,6,7].filter(i=>i===orderDetail.status).length" @click='handleRePay(orderDetail)'>再次购买</div>-->
        <!--<div class="btn toSettle" v-if="[4].filter(i=>i===orderDetail.status).length || ([5].filter(i=>i===orderDetail.status).length && dateDiff(orderDetail.policy_expiration_date))">申请理赔</div>-->
        <!--<div class="btn toPay" v-if="orderDetail.status===0" @click='handleToPay(orderDetail)'>立即支付</div>-->
        <!--<div class="btn rePay" v-if="[8].filter(i=>i===orderDetail.status).length" @click='handleToPay(orderDetail)'>重新支付</div>-->
        <!--<div class="btn toDetail" v-if="[0,1,2,3,4,5,6,7,8].filter(i=>i===orderDetail.status).length" @click="goDetail(orderDetail)">查看详情</div>-->
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { groupStatus, getSexByName, dateFormat } from '@/assets/js/mixins/base.js'

export default {
  mixins: [groupStatus, getSexByName, dateFormat],
  data () {
    return {
      // moreArr: [],
      detail: {},
      bzfaShow: true,
      bttrShow: false,
      ttrShow: false,
      orderDetail: {},
      detail_member: {},
      detail_guarantee: {},
      baseSrc: '',
      bzfaInfo: {},
      moreArr: [],
      upImg: {
        'background-image':
          'url(' + require('@/assets/images/icon_up.png') + ')'
      },
      downImg: {
        'background-image':
          'url(' + require('@/assets/images/icon_down.png') + ')'
      }
    }
  },
  mounted () {
    this.getGuarantyDetail()
    this.baseSrc = `${process.env.VUE_APP_RESOURCE_URL}`
    //
  },
  methods: {
    goClaim (item) {
      this.$router.push({
        path: '/claimList/claimApply/',
        query: {
          groupOrderSn: item.order_sn
        }
      })
    },
    getGuarantyDetail () {
      let postData = {
        id: this.$route.query.guarantyId
      }
      this.$api.personCenter.getGroupInsuranceDetail(postData).then(res => {
        if (res.code === 1) {
          this.detail = res.data
          this.detail_member = res.data.member
          if (res.data.guarantee === null) return
          this.detail_guarantee = res.data.guarantee
          if (res.data.guarantee) {
            this.bzfaInfo = res.data.guarantee.details
            // for(let i=0;i<this.bzfaInfo.len)
            for (let i = 0; i < this.bzfaInfo.length; i++) {
              this.moreArr.push(false)
            }

          }
        }
      })
    },
    detailShow (index) {
      this.moreArr.forEach((item, i) => { // 循环已经把所有的状态值清空了
        if (index !== i) {
          this.moreArr.splice(i, 1, false)
        }
      })
      this.moreArr.splice(index, 1, !this.moreArr[index])
    },
    handleRePay (item) { // 再次购买
      this.$router.push({
        query: {
          productId: item.product_id,
          orderSn: item.sn
        },
        path: `/product/detail/${item.product_id}`
      })
    },
    handleToPay (item) { // 重新支付 //立即支付
      this.$router.push({
        query: {
          sn: item.sn,
          productId: item.product_id
        },
        path: `/product/orderConfirm`
      })
    },
    goDetail (item) {
      this.$router.push({
        path: `/product/detail/${item.id}`,
        query: {
          orderId: item.id,
          productId: item.product_id
        }
      })
    }

  },
  computed: {
    ...mapGetters(['userInfo'])
  }
}
</script>

