            .oneDiv{
              border-bottom: none;
            }
          }
        }
        .plan-info-item {
          .oneDiv {
            display: flex;
            justify-content: space-between;
            border-bottom: 1px solid #D5D8D8;
            padding-right: pxToRem(20px);
            height: pxToRem(86px);
            line-height: pxToRem(86px);

          }
          .twoDiv {
            font-size: pxToRem(24px);
            color: #A5A8A8;
